n-primary:hover:active {\n  background-color: var(--button-primary-active-bgcolor);\n}\n\n.PanelUI-profiler-shortcut {\n  padding-block-start: 5px;\n  opacity: 0.5;\n  text-align: center;\n}\n\n.PanelUI-profiler-buttons {\n  margin: 7px 10px 0;\n}\n\n.PanelUI-profiler-info-icon {\n  margin-inline-end: 10px;\n  margin-block-start: 25px;\n  width: 50px;\n  list-style-image: url(chrome://devtools/skin/images/tool-profiler.svg);\n  -moz-context-properties: fill;\n  fill: #fff;\n}\n\n#PanelUI-profiler-presets[disabled=\"true\"]::part(label-box) {\n  opacity: 0.5;\n}\n\n/* Web-extension pop-ups */\n\n.cui-widget-panelview[id^=PanelUI-webext-] {\n  border-radius: var(--arrowpanel-border-radius);\n}\n\n.webextension-popup-browser,\n.webextension-popup-stack {\n  border-radius: inherit;\n}\n\n.webextension-popup-stack {\n  position: relative;\n}\n", "",{"version":3,"sources":["webpack://./../../themes/shared/customizableui/panelUI-shared.css"],"names":[],"mappings":"AAAA;;6DAE6D;;AAE7D;EACE,uDAAuD;EACvD,yDAAyD;EACzD,8CAA8C;EAC9C,2DAA2D;EAC3D,8DAA8D;EAC9D,gEAAgE;EAChE,qDAAqD;EACrD,8CAA8C;;EAE9C,2BAA2B;EAC3B,6BAA6B;;EAE7B,qCAAqC;EACrC,wCAAwC;EACxC,8GAA8G;EAC9G,wCAAwC;EACxC,yCAAyC;EACzC,iHAAiH;EACjH,wCAAwC;EACxC,0CAA0C;EAC1C,uGAAuG;EACvG,0CAA0C;EAC1C,oDAAoD;EACpD,uGAAuG;EACvG,0IAA0I;EAC1I,sCAAsC;EACtC,wCAAwC;EACxC,yGAAyG;EACzG,uCAAuC;EACvC,sCAAsC;EACtC,8GAA8G;;EAE9G,mCAAmC;AACrC;;AAEA;EACE,gGAAgG;AAClG;;AAEA;EACE,wCAAwC;EACxC,wCAAwC;EACxC,0CAA0C;EAC1C,oDAAoD;EACpD,wCAAwC;EACxC,uCAAuC;AACzC;;AAEA;EACE,aAAa;EACb,YAAY;EACZ,WAAW;EACX,wBAAwB;EACxB,YAAY;AACd;;AAEA;;EAEE,aAAa;AACf;;AAEA;;;;;;EAME,gBAAgB;EAChB,wEAAwE;EACxE,2BAA2B;EAC3B,kCAAkC;EAClC,eAAe;EACf,gBAAgB;EAChB,6BAA6B;EAC7B,gFAAgF;EAChF,qBAAqB;AACvB;;AAEA;;;;;EAKE,uDAAuD;AACzD;;AAEA;EACE,gFAAgF;EAChF,iCAAiC;AACnC;;AAEA;;;;;EAKE,6DAA6D;EAC7D,4BAA4B;EAC5B,2BAA2B;EAC3B,6BAA6B;EAC7B,uDAAuD;AACzD;;AAEA;EACE,wEAAwE;EACxE,qCAAqC;EACrC,oCAAoC;EACpC,iCAAiC;AACnC;;AAEA;;;EAGE,YAAY;EACZ,sGAAsG;EACtG,gBAAgB;EAChB,gBAAgB;EAChB,6BAA6B;EAC7